<!--  -->

<template>
	<section class="pull-list" v-touch:pandown="pullToRefresh" v-touch:panup="loadMore" v-touch:panend="panleave"   >
		<header class="pull-header">
			<div>
				<img class="downImg" src="../assets/images/components/icon-down.png" alt="">
			</div>
			<div>
				<p>{{refreshText}}</p>
				<p>:{{rlastTime}}</p>
			</div>
		</header>
		<div class="pull-content" v-on:scroll="scroll($event)" v-el:listContent>
			<slot ></slot>
		</div>
		<footer class="pull-footer" v-show="showfooter">
			<div>
				<img class="upImg" src="../assets/images/components/icon-up.png" alt="">
			</div>
			<div>
				<p>{{moreText}}</p>
				<p>:{{mlastTime}}</p>
			</div>
		</footer>
	</section>
	
</template>

<script lang="babel">

	import dateHelper from './utils/DateHelper.js'

	export default {
		data(){
			return {
				rlastTime:'',
				mlastTime:'',
				refreshText:'',
				moreText:'',
				//
				isRefresh:false,
				//
				showfooter:false,
				//
				arrow:'down',
				//
				top:0,
				//
				isBottom:false,
				//
				regionHeight:100,
				//
				winHeight:100
			}
		},
		ready(){
			//
			this.regionHeight=this.$el.offsetHeight;
			var listObj=this.$el.getElementsByTagName('ul')[0];
			this.winHeight=listObj.offsetHeight;
			console.log(""+this.regionHeight+"-:"+this.winHeight);

		},
		methods:{

			move(height){
				var list=this.$el;
				//
				var rotateHeight=height<90?height*2:180;
				if(this.arrow=="down"){
					list.style.transform="translateY("+height+"px)";
					var img=list.getElementsByTagName('header')[0].getElementsByTagName('img')[0];
					img.style.transform="rotate("+rotateHeight+"deg)"
				}else{
					list.style.transform="translateY(-"+height+"px)";
					//div,,
					if(list.getElementsByTagName('footer')[0]){
						var img=list.getElementsByTagName('footer')[0].getElementsByTagName('img')[0];
						img.style.transform="rotate("+rotateHeight+"deg)"
					}
				}
			},
			//,
			scroll(e){
				this.top=e.target.scrollTop;
				if(this.top+this.regionHeight>this.winHeight){
					this.isBottom=true;
				}else{
					this.isBottom=false;
				}
			},
			getCurTime(){
				 var date=new Date();
				// return date.getFullYear()+""
			},
			panleave(e){
				
				if(this.arrow=="down"){
					this.move(0)
					this.refreshText="";
					//
					if(this.isRefresh){
						this.isRefresh=false;
						this.rlastTime=dateHelper.getNowDate("yyyy-MM-dd HH:mm:ss");
						this.$emit('reload');
					}
					// this.$broadcast('list-reload');
				}else if(this.arrow=="up"){
					this.showfooter=false;
					this.moreText="";
					this.move(0)
					if(this.isRefresh){
						this.isRefresh=false;
						this.mlastTime=dateHelper.getNowDate("yyyy-MM-dd HH:mm:ss");
						this.$emit('loadmore');
					}
				}
			},
			// 
			pullToRefresh(e){
				if(this.top==0){
				 	var distance=e.distance;
				 	this.arrow='down';
				 	this.move(distance);
				 	if(distance>90){
						this.refreshText="";
				 		this.isRefresh=true;
				 	}
				}
			},
			//
			loadMore(e){
				//+
				if(this.isBottom){
					this.showfooter=true;
					this.arrow='up';
					var distance=e.distance;
				 	// console.log(""+distance)
				 	this.move(distance);
				 	if(distance>90){
						this.moreText="";
				 		this.isRefresh=true;
				 	}
				}
			}
		}
	}
</script>


<style >

	/*list*/
	.pull-list{
		display: flex;
	   	flex-flow:column nowrap;
	  	min-height: 120px;
	  	justify-content:flex-start;
	  	flex:0 1 auto;
	   	height: 100%;
	}

	
	.pull-header,.pull-footer{
	     display: flex;
     	 flex-flow:row nowrap;
	     min-height: 60px;
	     height: 60px;
	     justify-content:center;
	     flex:0 1 auto;
	     align-items:center;
	     aiign-content:center;
	}

	.pull-header{
	    
		margin-top: -60px;
		border-bottom: 1px solid whitesmoke;

	}
	.pull-footer{
		margin-bottom: -60px;
		border-top: 1px solid whitesmoke;
	}

	.pull-header>img,.pull-footer>img{
		 transition: transform .3s ease;
	      -moz-transition: transform .3s ease;/* Firefox 4 */
	      -webkit-transition: transform .3s ease; /* Safari  Chrome */
	      -o-transition: transform .3s ease; /* Opera */
	}


	.pull-header>div,.pull-footer>div{
		font-size: 12px;
		margin-left: 12px;
	}
	

	.pull-content{
		overflow: auto;
	    -webkit-overflow-scrolling: touch;
	    display: -webkit-flex;
	    display: -ms-flexbox;
	    display: flex;
	    -webkit-flex: 0 1 auto;
	    -ms-flex: 0 1 auto;
	    flex: 0 1 auto;
	    margin-bottom: 10px;
	    margin: 0px;
	    flex-flow:column nowrap;
	    padding: 0px;
	}
</style>


